* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
}

nav {

  height: 90px;
  width: 100%;
  display: flex;
  padding: 20px 36px;
  box-shadow: 0px 2.98256px 7.4564px rgba(0, 0, 0, 0.1);
}

.nav--logo {
  width: 150px;
}

section {
  padding: 20px;
}

.content {
  display: flex;
  flex-direction: column;
}

.content--photo {
  max-width: 600px;
  align-self: center;
  border-radius: 30px;
}

.content--header {
  margin-bottom: 0px;
  align-self: center;
  text-transform: uppercase;
}

.content--text {
  text-transform: uppercase;
  margin-top: 0;
  align-self: center;
}

.navMenu {
  position: relative;
  top: 30%;
  left: 30%;
}

.navMenu a {
  color: #000000;
  text-decoration: none;
  font-size: 1.2em;
  text-transform: uppercase;
  font-weight: 500;
  display: inline-block;
  padding-left: 30px;
}

.container {
  position: relative;
  left: 50%;
}

.rounded-button {
  padding: 20px;
  text-transform: uppercase;
  background-color: #4CAF50;
  color: white;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 18px;
  font-weight: bold;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 0;
}

.rounded-button:hover {
  background-color: #45a049;
}

.tutors {
  position: relative;
  margin: 0;
  padding: 20px;
  padding-left: 400px;
  width: 100%;
  background-color: #f1efef;
  text-transform: uppercase;
  display: flex;
  align-items: center;
}

.tutors--photo {
  max-width: 250px;
  border-radius: 30px;
  margin-right: 20px;
}

.photo-container {
  height: 250px;
}